.ucloud-table{
  border:solid 1px #eee;
  border-bottom: 0;
  border-right: 0;
  position: relative;
  table{
    table-layout: fixed;
  }
  th{
    border-bottom: solid 1px #eee;
    border-right: solid 1px #eee;
    background-color: #FFF;
  }
  td{
    border-bottom: solid 1px #eee;
    border-right: solid 1px #eee;
    background-color: #FFF;
  }
  .ucloud-table-head{
    overflow: hidden;
  }
  .ucloud-table-fixed{
    width: 100%;
    overflow: hidden;
    position: absolute;
    top: 0;
    left: 0;
    box-shadow: 2px 0 6px -2px rgba(0,0,0,.2);
    .visibilityhidden{
      visibility: hidden;
    }
    .ucloud-table-fixed-body{
      overflow: hidden;
      position: relative;
    }
  }
  .ucloud-table-body{
    overflow: auto;
  }
}



